@import url(https://fonts.googleapis.com/css?family=Quicksand:400,700&display=swap);
body{margin:0;background-color:#5a7b7b}.App{display:flex;justify-content:center;align-items:center;height:100vh}#quote-box{font-family:"Quicksand",sans-serif;background-color:#fff;box-sizing:border-box;width:800px;padding:1rem 2rem;border-radius:20px;display:flex;flex-direction:column;justify-content:center;align-items:center}#quote-box h1{margin:0;font-size:1.5rem;text-align:center;font-weight:400}#quote-box h4{font-size:1.1rem;font-weight:400}.buttons{display:flex;flex-direction:row;justify-content:space-evenly;width:35%}.buttons #tweet-quote,.buttons #new-quote{font-family:inherit;text-decoration:none;border:1px solid #5a7b7b;border-radius:2px;padding:0.5rem 1rem;font-size:1rem;color:#5a7b7b;background:-webkit-gradient(linear, left top, left bottom, color-stop(50%, #fff), color-stop(50%, #5a7b7b));background:linear-gradient(to bottom, #fff 50%, #5a7b7b 50%);background-size:100% 200%;background-position:left top;-webkit-transition:all 250ms ease-in-out;transition:all 250ms ease-in-out}.buttons #tweet-quote:hover,.buttons #new-quote:hover{cursor:pointer;color:#fff;background-position:left bottom}.loader{padding:3rem}

